import "./home.css";
import React, { useReducer } from "react";
import { Table } from "react-bootstrap";
import Card from "react-bootstrap/Card";
import icon from './icon.jpg';
import { useSelector} from "react-redux"


function Home() {
  const newData=useSelector(state=>state.data);
  
  // const users = [
  //   { Sr_no: "1 ", Name: "Player 1", Points: "5 points" },
  //   { Sr_no: "2", Name: "Kenrick VAz", Points: "5 points" },
  //   { Sr_no: "3", Name: "Player 3", Points: "5 points" },
  // ];
  
  return (
    <div className="App center-div">
    
        <h2 className="heading">Leaderboard</h2>
        <p className="text"> Last updated : 23 minutes ago</p>

        <Card className="card card-pos col-xs-12 col-sm-12 col-md-8 col-lg-8 " >
          <Table className="table table-color" striped variant="dark">
            <tbody>
              {/* <tr>
                <td>Sr.no</td>
                <td> Name</td>
                <td>Points</td>
              </tr> */}
              {newData.map((item, i) =>
                 (
                  <tr key={i}>
                    <td className="txt-color">{i+1}</td>
                    <td className="txt-color">
                      <img
                        className="icon-image"
                        src={icon}
                        height={50}
                        width={50}
                      />
                      <span /> {item.Name}
                    </td>
                    <td className="txt-color">{item.Points} Points</td>
                  </tr>
                )
              )}
            </tbody>
          </Table>
        </Card>
      </div>

  );
}

export default Home;
